import ProductList from '../components/ProductList';
import Nav from '../components/Navbar';
import Carousel from '../components/carousel';
import Footer from '../components/footer';

function Home({cartProduct,addToCart,setLoginUser,product,setshowLoader}) {

  return (
   <>
   <Nav cartCount={cartProduct.length} setLoginUser={setLoginUser} setshowLoader={setshowLoader} ></Nav>
   <Carousel></Carousel>
   <ProductList products={product} addToCart={addToCart} cartProduct={cartProduct}></ProductList>
   <Footer></Footer>
   </>
  );
}

export default Home;