import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import styles from './loginSignup.module.css';
import cart from "./Icons/check.png";

function LoginSignup({login, signUp}){
    const blankUser = {
        userName : "",
        email : "",
        password : ""
    }
    const [showLogin, setShowLogin] = useState(true);
    const [user , setUser] = useState(blankUser);
  

    

    useEffect(()=>{
        setUser(blankUser);
    },[showLogin])

    function handleChange(e){
        setUser({...user , [e.target.name]:e.target.value})

    }
    function validateLogin(user){
        if(!user.email || !user.password){
            toast.error('Please enter valid fields', {
              position: "top-right",
              autoClose: 2000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              theme: "colored",
              });
        }else if (!user.email.match(/[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/)) {
            toast.warn('Please enter valid Email', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }else if(user.password.length<6){
            toast.warn('Password length should be more than 6 characters', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }
        else{
            login(user);
        }
    }
    function validateSignup(user){
        if(!user.email || !user.password || !user.userName){
          toast.error('Please enter valid fields', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });        
          }else if (!user.email.match(/[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/)) {
            toast.warn('Please enter valid Email', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }else if(user.password.length<6){
            toast.warn('Password length should be more than 6 characters', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }
          else{
            signUp(user);
        }
    }

    
    

    return(
     <>
     {showLogin?<div className={styles.container}>
        <div className={styles.login}>
            <div>
            <h2>Login</h2>
            <div className={styles.inputs}>
                <div className={styles.email}>
                    <label>Email</label>
                    <input type="email" placeholder="Email" name="email" value={user.email} onChange={handleChange}/>
                </div>
                <div className={styles.password}>
                    <label>Password</label>
                    <input type="password" placeholder="Password" name="password" minlength="6" value={user.password} onChange={handleChange}/>
                </div>
                <button onClick={()=>validateLogin(user)}>Login</button>
                <img src={cart}></img>
            </div>
            </div>
            <div className={styles.notAccount}>
                <p>Don't have Account?</p>
                <span onClick={()=>setShowLogin(false)}>SIGN UP</span>
            </div>
        </div>
     </div>:
     <div className={styles.container}>
     <div className={styles.login}>
         <div>
         <h2>Sign Up</h2>
         <div className={styles.inputs}>
            <div className={styles.username}>
                 <label>Username</label>
                 <input type="text" placeholder="Full Name" name="userName" value={user.userName} onChange={handleChange}/>
             </div>
            <div className={styles.email}>
                 <label>Email</label>
                 <input type="email" placeholder="Email" name="email" value={user.email} onChange={handleChange}/>
             </div>
             <div className={styles.password}>
                 <label>Password</label>
                 <input type="password" placeholder="Password" name="password" minlength="6" value={user.password} onChange={handleChange}/>
             </div>
             <button onClick={()=>validateSignup(user)}>Sign Up</button>
             <img src={cart}></img>
         </div>
         </div>
         <div className={styles.notAccount}>
             <p>Have an Account?</p>
             <span onClick={()=>setShowLogin(true)}>LOGIN</span>
         </div>
     </div>
  </div>
     }
     </>
    )
}
export default LoginSignup;