import { Link } from "react-router-dom";
import dashboard from "./Dashboard Icons/layout.png";
import products from "./Dashboard Icons/pallets.png";
import createProduct from "./Dashboard Icons/shipping.png";
import cart from "./Dashboard Icons/shopping-cart.png";
import user from "./Dashboard Icons/user.png";
function SideBar() {
  return (
    <>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          gap: 10,
          width: 163,
          paddingLeft: 10,
          paddingTop:10,
          // paddingRight:10,
          backgroundColor:'white',
          height:'87.4vh'
        }}
        
      >
        <Link to={"/admin"} style={{ textDecoration: "none", color: "black" }}>
          <p className="sideBarHoverAction">
            {" "}
            <img src={dashboard} style={{ width: 20 }}></img> Dashboard
          </p>
        </Link>
        <Link
          to={"/createProduct"}
          style={{ textDecoration: "none", color: "black" }}
        >
          <p className="sideBarHoverAction">
            {" "}
            <img src={createProduct} style={{ width: 20 }}></img> Create Product
          </p>
        </Link>
        <Link
          to={"/adminProducts"}
          style={{ textDecoration: "none", color: "black" }}
        >
          <p className="sideBarHoverAction">
            {" "}
            <img src={products} style={{ width: 20 }}></img> Products
          </p>
        </Link>
        <Link
          to={"/adminOrders"}
          style={{ textDecoration: "none", color: "black" }}
        >
          <p className="sideBarHoverAction">
            {" "}
            <img src={cart} style={{ width: 20 }}></img> Orders
          </p>
        </Link>
        <Link
          to={"/adminUsers"}
          style={{ textDecoration: "none", color: "black" }}
        >
          <p className="sideBarHoverAction">
            {" "}
            <img src={user} style={{ width: 20 }}></img> Users
          </p>
        </Link>
      </div>
    </>
  );
}
export default SideBar;
