import * as React from 'react';
import { BarChart } from '@mui/x-charts/BarChart';


export default function StackedBarChart({allOrders}) {
    const uData = []  
    const xLabels = []
const sales = new Map();
 allOrders.map(order=>order.items.map(v=>v.slice(0,-1).map(val=>{
    if(sales.has(val.category)){
        sales.set(val.category,sales.get(val.category)+val.quantity) 
    }else{
        sales.set(val.category,val.quantity)
    }
 })))
 sales.forEach(function(value,key){
    xLabels.push(key)
    uData.push(value)
 })
 
  return (
  <>
    {xLabels.length>0 && uData.length>0 ?<BarChart
      width={620}
      height={320}
      series={[
        { data: uData, label: 'Number of sales of each Category', id: 'uvId', stack: 'total' },
      ]}
      xAxis={[{ data: xLabels, scaleType: 'band' }]}
    />:null}
    </>
  );
}