import { Link, useNavigate } from "react-router-dom";

function ProductList({ products, addToCart,cartProduct }) {
  const navigate = useNavigate()
  return (
    <>
      <div className="container mb-5">
        <div id="products" className="row">
          {products.length>0 && products.map((product) => (
            
            <div className="col-xl-3 col-lg-4 col-md-6 position-relative">
              <div style={{border:'none'}}  className="card product-item">
                <i className="bi bi-heart-fill position-absolute liked"></i>
                <i className="bi bi-heart position-absolute like"></i>
                <Link to={`/productDetails/${product._id}`}
                state={{ productDetail: product ,cartProduct:cartProduct}}>
                  <img
                    // src={`images/${product.image}.jpg`}
                    // src={`productImages/${product.image}`}
                    src={`https://ecommerce-mern-stack-5vy4.onrender.com/${product.image[0]}`}
                    
                    className="card-img-top"
                    alt="..."
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="Click to See Product Details"
                  />
                  {/* <image src={product.image}></image> */}
                </Link>
                <div className="card-body">
                  <h6 className="card-subtitle mb-2 text-muted  fw-light">
                    {product.category}
                  </h6>
                  <h5 className="card-title">{product.name}</h5>
                  <p className="card-text price">
                    {" "}
                    ${product.price}
                    <p className="float-end rating-stars">
                      <i className="bi bi-star-fill"></i>
                      <i className="bi bi-star-fill"></i>
                      <i className="bi bi-star-fill"></i>
                    </p>{" "}
                  </p>
                  {product.stock<=0?
                  <span style={{color:'red',fontSize:25}}>Out of stock</span>: 
                  cartProduct.length >=0  && cartProduct.find(x=>x._id===product._id) === undefined ?<div className="text-center">
                      <a
                      className="btn btn-dark w-100"
                      onClick={() => addToCart(product)}
                      role="button"
                    >
                      {" "}
                      Add To Cart
                    </a> 
                     </div>:<div className="text-center">
                      <a
                      style={{backgroundColor:'rgb(25,135,84)',border:'none'}}
                      className="btn btn-dark w-100"
                      onClick={() => ( navigate("/cart"))}
                      role="button"
                    >
                      {" "}
                      Go To Cart
                    </a> 
                     </div> }
    
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
}
export default ProductList;
