import { Link } from "react-router-dom";
import AdminDashboard from "./adminDashboard";
import Table from 'react-bootstrap/Table';
import { format } from 'date-fns';
import SideBar from "./Dashboard/SideBar";

function AdminUsers({allUsers,deleteUser}){
    let i=1;
    return(
        <>
        <AdminDashboard></AdminDashboard>
        
        <div className="userTable" >
      <SideBar></SideBar>
      <div style={{width:"100%"}}>
      <h1>USERS LIST</h1>
        <Table>
        <thead>
          <tr>
            <th>#</th>
            <th>User ID</th>
            <th>Email</th>
            <th>Name</th>
            <th>Joining Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        {allUsers.map(usr=>(  
            <tbody>
            <tr>
              <td>{i++}</td>
              <td>{usr._id}</td>
              <td>{usr.email}</td>
              <td>{usr.userName}</td>            
              <td>{format(new Date(usr.createdAt), 'dd/MMM/yyyy')}</td>
              <td> 
              {usr.email!=='altafbagwan808@gmail.com'?<i className="fa fa-trash-o hover" onClick={()=> deleteUser(usr._id)}  style={{fontSize:30,color: "red",marginLeft: 10}}></i>:
              <span style={{color:'green',fontWeight:500,cursor:'pointer'}}>Owner</span>}
              </td>
            </tr>
          </tbody>
        )) }
      </Table>
      </div>
      </div>
        </>
    )
}
export default AdminUsers;