import Nav from "../components/Navbar";
import Footer from "../components/footer";
import Order from "../components/order";
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";
import axios from 'axios';
axios.defaults.withCredentials = true;

function OrderPage(props){
  const {cartProduct , orderProduct ,setOrderProduct,showOrders,shippAddress,getOrders,setLoginUser,loginUser,setshowLoader} = props
  useEffect(()=>{
    getOrders();
  },[]) 
const navigate = useNavigate()
if(!loginUser){
  navigate('/login')
}
    return(
        <>
    <Nav cartCount={cartProduct.length } showOrders={showOrders} setLoginUser={setLoginUser} setshowLoader={setshowLoader}></Nav>
     {Object.keys(orderProduct).length !== 0? <Order orderProduct={orderProduct} setOrderProduct={setOrderProduct} cartProduct={cartProduct} shippAddress={shippAddress}></Order>:null}
   {/* <Footer></Footer> */}
        </>
    )
}

export default OrderPage;