
import Nav from '../components/Navbar';

import Footer from '../components/footer';
import Checkout from '../components/checkOut';
import { useNavigate } from 'react-router-dom';
import { useEffect,useState } from 'react';
import axios from 'axios';

axios.defaults.withCredentials = true;
function CheckoutPage({cartProduct, orderProduct , setOrderProduct,loginUser,setLoginUser,shippAddress,setShippingAddress,emptyCart,setshowLoader}) {
  const navigate = useNavigate();
  const [ListOfAddress , setListAddress] = useState([]);
  useEffect(()=>{
  getAddresses();
  },[])
  if(!loginUser){
    navigate('/login')
}
  function getAddresses(){
    setshowLoader(true);
    axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/address').then(function (response) {
      // handle success
      setListAddress(response.data);
      setshowLoader(false);
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
   
  return (
   <>
   
   <Nav cartCount={cartProduct.length} setLoginUser={setLoginUser} setshowLoader={setshowLoader}></Nav>
   <Checkout cartProduct={cartProduct} orderProduct={orderProduct} setOrderProduct ={setOrderProduct} loginUser={loginUser} shippAddress={shippAddress} setShippingAddress={setShippingAddress} ListOfAddress={ListOfAddress} setListAddress={setListAddress} emptyCart={emptyCart} setshowLoader={setshowLoader} ></Checkout>
   <Footer></Footer>
   </>
  );
}

export default CheckoutPage;