import AdminDashboard from "../adminDashboard";
import StackedBarChart from "./BarGraphDashboard";
import dashboardStyles from "./Dashboard.module.css";
import BasicPie from "./PieChartDashboard";
import SideBar from "./SideBar";
import cardProduct from "./Dashboard Icons/pallets.png"
import usr from "./Dashboard Icons/user.png";
import cart from "./Dashboard Icons/shopping-cart.png";
import cardStyles from "./cardDashboard.module.css";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

function Dashboard({ products, allUsers, allOrders,loginUser}) {

  const navigate = useNavigate();
    if(!loginUser){
        navigate('/login')
    }
  let totalStock = 0;
  let totalOrders =0;
  let totalAmount =0;
  products.map(val => {
    totalStock = totalStock + val.stock;
  });
  allOrders.map(order=>{
    totalOrders=totalOrders+order.items.length
    order.items.map(v=>{
      totalAmount = totalAmount + v[v.length-1].amount
    })
  })
  let cost = totalAmount/2+153
  return (
    <>
    <div className={dashboardStyles.section} >
      <AdminDashboard></AdminDashboard>
      <div className={dashboardStyles.container}>
        <div>
          <SideBar></SideBar>
        </div>
        <div className={dashboardStyles.middleContainer}>
          <div className={cardStyles.amountHeading}>
            <p>Total Payments: ${parseFloat(totalAmount).toFixed(2)}</p>
          </div>
          <div className={dashboardStyles.cards}>
            <div className={cardStyles.container}style={{backgroundColor:'rgb(13, 234, 149)'}}>
              <div className={cardStyles.cardDetails} >
                <h2>{totalStock}</h2>
                <p>Total Stock</p>
                <p>All types of Products</p>
              </div>
              <div>
                  <img src={cardProduct}></img>
              </div>
            </div>
            <div className={cardStyles.container}style={{backgroundColor:'rgb(235, 213, 15)'}}>
              <div className={cardStyles.cardDetails} >
                <h2>{allUsers.length}</h2>
                <p>Total Users</p>
                <p>All Registered users</p>
              </div>
              <div>
                  <img src={usr}></img>
              </div>
            </div>
            <div className={cardStyles.container}style={{backgroundColor:'rgb(195, 15, 235)'}}>
              <div className={cardStyles.cardDetails} >
                <h2>{totalOrders}</h2>
                <p>Total Orders</p>
                <p>All Orders by users</p>
              </div>
              <div>
                  <img src={cart}></img>
              </div>
            </div>
          </div>
          <div className={dashboardStyles.barChart}>
          <StackedBarChart allOrders={allOrders}></StackedBarChart>
          </div>
        </div>
        <div className={dashboardStyles.rightContainer}>
        <div className={dashboardStyles.pieChart}>
          <BasicPie products={products}></BasicPie>
          {/* <PieChartWithCenterLabel></PieChartWithCenterLabel> */}
        </div>
        <div className={dashboardStyles.summary}>
            <h4>Summary</h4>
            <div style={{display: 'flex',justifyContent:'space-between'}}>
              <p>Total Quantity</p>
              <p style={{color: 'blue'}}>{totalStock} Qt.</p>
            </div>
            <div style={{display: 'flex',justifyContent:'space-between'}}>
              <p>Total Revenue</p>
              <p style={{color: 'orange'}}>${parseFloat(totalAmount).toFixed(2)}</p>
            </div>
            <div style={{display: 'flex',justifyContent:'space-between'}}>
              <p>Total Cost</p>
              <p style={{color: 'red'}}>${parseFloat(cost).toFixed(2)}</p>
            </div>
            <div style={{display: 'flex',justifyContent:'space-between'}}>
              <p>Total Profit</p>
              <p style={{color: 'green'}}>${parseFloat(totalAmount-cost).toFixed(2)}</p>
            </div>
          </div>
          {/* <div className={dashboardStyles.rating}>

          </div> */}
      </div>
      </div>
      </div>
    </>
  );
}
export default Dashboard;
