import { useEffect } from "react";
import Nav from "../components/Navbar";
import Cart from "../components/Cart/cart";
import Footer from "../components/footer";
import axios from 'axios';
import { useNavigate } from "react-router-dom";

function CartPage({cartProduct,newCartProduct,deleteCartItem,setshowLoader,showLoader,setLoginUser,loginUser,product}){
    const navigate = useNavigate()
    if(!loginUser){
        navigate('/login')
    }
    
    let no = (<div style={{display:'flex',width:'100vw',height:'60vh',alignItems:'center', justifyContent:"center"}}>
        <h1 style={{color:'grey'}}>No Cart Items</h1>
    </div>)
    return(
        <>
    <Nav cartCount={cartProduct.length} setLoginUser={setLoginUser} setshowLoader={setshowLoader}></Nav>
   {cartProduct.length > 0 ? <Cart product={product} cartProduct={cartProduct} newCartProduct={newCartProduct} deleteCartItem={deleteCartItem} showLoader={showLoader} setshowLoader={setshowLoader}></Cart>:no}
           </>
    )
}

export default CartPage;