import { useEffect, useRef, useState } from "react";
import userStyles from "./userProfile.module.css"
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import Swal from 'sweetalert2'


function UserProfile({loginUser,updateUser,allUsers,setAllUsers,deleteUser,setshowLoader}){
    const inputRef = useRef(null);
    const navigate = useNavigate();
    const [showEdit,setShowEdit] = useState(false);
    const [showEditPassword , setShowEditPassword] = useState(false);
    const [editUser , setEditUser] = useState()
    if(!loginUser){
        navigate('/login')
    }
    const [image,setImage]= useState();
    const [imageName,setImageName] = useState('');
    function handleImageClick(){
        if(!showEdit){
            inputRef.current.click();
        }
    }
    function handleUploadProfile(e){
        e.preventDefault();
        if(image=== undefined){
            toast.error('Please Choose Profile Picture first', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }
        else{
            setshowLoader(true);
        let formData = new FormData();
        formData.append('profile', image);
        axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/profile',formData).then(function (response) {
      // handle success
      if(response.status===200){
        setImageName(response);
        setImage();
        toast.success('Profile Uploaded Successfully', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
            setshowLoader(false);
       }
     
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })
}
    }
    useEffect(()=>{
        addingImageNameinUserobj();
    },[imageName])
    function addingImageNameinUserobj(){
        if(imageName!==''){ 
            setshowLoader(true);
        axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/profileName/${loginUser._id}`,imageName).then(function (response) {
          // handle success
          if(response.status===200){
            const index=allUsers.findIndex(value=>value._id===loginUser._id)
            allUsers.splice(index,1,response.data) //splice method direct mutate the array and direct mutating the state in react dose not rerender the component
          //so we do some jugaad by copying the array and set it, this change the state and Render the Component
          let copy_userProfile_Array = allUsers;
          setAllUsers([...copy_userProfile_Array]);
          setshowLoader(false);
           }
        })
        .catch(function (error) {
          // handle error
          setshowLoader(false);
          console.log(error);
        })
    }
    }
    function profileChange(e){
        setImage(e.target.files[0]);
    }
    let userObj={};
    if(allUsers.length>0){
    allUsers.map(usr=>{
        if(usr._id===loginUser._id){
            userObj=usr;
        }
    })}
    function handleChange(e){
        setEditUser({...editUser,[e.target.name]:e.target.value})
    }
    function handleSave(){
        if(editUser!==undefined){
            if(editUser.email && !editUser.email.match(/[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/)){
                setEditUser();
                toast.error('Please enter valid email', {
                    position: "top-right",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "colored",
                    });
            }else{
                updateUser(editUser,loginUser._id)
        setEditUser();
            }
        
    }else{
        toast.info('No Changes have done', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
    }
        setShowEdit(false);

        
    }
    function handleSavePass(){
        let a = document.querySelector("#oldPassword").value;
        let b = document.querySelector("#confirmPassword").value;
        if(userObj.password === a && b===editUser.password){
        updateUser(editUser,loginUser._id)
        setShowEditPassword(false)
        toast.success('Password Changed Successfully', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
        }else if(a.length <6 || b.length <6){
            toast.warn('Password length should be more than 6 characters', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }
        else if(userObj.password !== a ){
            toast.error('Wrong Old Password', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }else{
            toast.error('Confirm Password is Incorrect', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }
    }
    function handleRemoveProfile(){
        if(userObj.profile===''){
            toast.error('There is no profile picture uploaded', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
        }else{
        setshowLoader(true);
        const str ="";
        axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/profileImage/${loginUser._id}`,str).then(function (response) {
          // handle success
          if(response.status===200){
            const index=allUsers.findIndex(value=>value._id===loginUser._id)
            allUsers.splice(index,1,response.data) //splice method direct mutate the array and direct mutating the state in react dose not rerender the component
          //so we do some jugaad by copying the array and set it, this change the state and Render the Component
          let copy_userProfile_Array = allUsers;
          setAllUsers([...copy_userProfile_Array]);
          toast.success('Profile picture removed Successfully', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
            setshowLoader(false);
           }
        })
        .catch(function (error) {
          // handle error
          setshowLoader(false);
          console.log(error);
        })
    }
    }

    function handleDeleteUser(){
        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!, also your all cart and order history will be deleted",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) => {
            if (result.isConfirmed) {
                deleteUser(loginUser._id);
              Swal.fire(
                'Deleted!',
                'Your Account has been deleted.',
                'success'
              )
            }
          })
        
    }
    return(
        <>
        {!showEdit?
        <div className={userStyles.containerrr}>
        {/* <h1>Profile</h1> */}
        
        <div className={userStyles.profile}>
            <div className={userStyles.profileImage}>
                <div onClick={handleImageClick}>
                {image?<img src={URL.createObjectURL(image)}></img>:userObj.profile!==""?<img src={`https://ecommerce-mern-stack-5vy4.onrender.com/${userObj.profile}`}></img>:<img src="images/user.png"></img>}
                {/* {image===undefined?<img src="images/user.png"></img>:image?<img src={URL.createObjectURL(image)}></img>:userObj.profile!==""?<img src={`https://ecommerce-mern-stack-5vy4.onrender.com/${userObj.profile}`}></img>:<img src="images/user.png"></img>} */}

                <input type="file" ref={inputRef} style={{display:"none"}}
                onChange={profileChange} name="profile"></input>
                </div>
                <button className={userStyles.btnn} onClick={handleUploadProfile}> Upload</button>
            </div>
            <div className={userStyles.profileDetails}>
                <div>
                    <strong>Full Name</strong>
                <p>{userObj.userName}</p>
                </div>
                <div>
                <strong>Email</strong>
                <p>{userObj.email}</p>
                </div>
                <div>
                <strong>Joined On</strong>
                <p>{new Date(loginUser.createdAt).toLocaleString('en-GB', {day:'numeric', month: 'long', year:'numeric'})}</p>
                </div>
                <button className={userStyles.btnn} onClick={()=>setShowEdit(true)}>Edit Profile</button>
                    <button className={userStyles.btnn} id={userStyles.deletBtn} onClick={handleDeleteUser}>Delete Account</button>
            </div>
        </div>
        </div>
        : showEditPassword?
        <div className={userStyles.containerrr}>
        {/* <h1>Change Password</h1> */}
        
        <div className={userStyles.profileChangePassword}>
            
            <div className={userStyles.profileDetails}>                
                 <label>Old Password : <input  type="password" id="oldPassword" minlength="6" name="oldPassword"></input>
                </label>              
                <label>New Password : <input  type="password" name="password" minlength="6"  onChange={handleChange}></input>
                </label>
                <label>Confirm Password : <input type="password" id="confirmPassword" minlength="6" name="confirmPassword"  ></input>
                </label>
                <button className={userStyles.btnn} onClick={handleSavePass}>Save Password</button>
                <p style={{color:"blue" , textDecoration:"underline"}} className="hover" onClick={()=>setShowEditPassword(false)}>Back to Profile ?</p>
            </div>
        </div>
        </div>
        :<div className={userStyles.containerrr}>
        {/* <h1>Edit Profile</h1> */}
        
        <div className={userStyles.profile}>
            <div className={userStyles.profileImage}>
                <div onClick={handleImageClick}>
                {image?<img src={URL.createObjectURL(image)}></img>:userObj.profile!==""?<img src={`https://ecommerce-mern-stack-5vy4.onrender.com/${userObj.profile}`}></img>:<img src="images/user.png"></img>}
                <input type="file" ref={inputRef} style={{display:"none"}}
                onChange={profileChange} name="profile"></input>
                </div>
                <button style={{backgroundColor:"rgb(33,37,41)",color:"white"}} className={userStyles.btnn} onClick={handleRemoveProfile}>Remove Profile Photo</button>
            </div>
            <div className={userStyles.profileDetails}>
                <input style={{padding:3}} type="text" name="userName" defaultValue={userObj.userName} onChange={handleChange}></input>
                <input style={{padding:3}} type="text" name="email" defaultValue={userObj.email} onChange={handleChange}></input>
                <button className={userStyles.btnn} onClick={handleSave}>Save Profile</button>
                <button className={userStyles.btnn} id={userStyles.deletBtn} style={{background:"orange",color:"black"}} onClick={()=>setShowEditPassword(true)}>Change Password</button>
                <p style={{color:"blue" , textDecoration:"underline"}} className="hover" onClick={()=>setShowEdit(false)}>Back to Profile ?</p>
            </div>
        </div>
        </div>}
        </>
    )
}
export default UserProfile;