import { Link } from "react-router-dom";
import AdminDashboard from "./adminDashboard";
import Table from 'react-bootstrap/Table';
import SideBar from "./Dashboard/SideBar";

function AdminProductsList({products,deleteProduct,deleteCartItem}){
    let i=1;
    // products.map(product=>{
    //   if(product.stock<=0){
    //     deleteCartItem(product._id)
    //   }
    // })
    return(
        <>
        <AdminDashboard></AdminDashboard>
        
        <div className="userTable">
          <SideBar style={{width:3000}}></SideBar>
          <div style={{width:"100%"}}>
          <h1>PRODUCT LIST</h1>
          
      <Table >
        <thead>
          <tr>
            <th>#</th>
            <th>Product ID</th>
            <th>Product Name</th>
            <th>Category</th>
            <th>Stock</th>
            <th>Price</th>
            <th>Actions</th>
          </tr>
        </thead>
        {products.map(product=>(  
            <tbody>
            <tr>
              <td>{i++}</td>
              <td>{product._id}</td>
              <td>{product.name}</td>
              <td>{product.category}</td>
              {product.stock>=0?<td>{product.stock}</td>:<td>0</td>}
              <td>{product.price}</td>
              <td> <Link to={`/editProduct/${product._id}`}
               state={{product:product}}
              > 
              <i className="fa fa-edit hover" style={{fontSize:28}}></i></Link>
              <i className="fa fa-trash-o hover" onClick={()=>deleteProduct(product._id)} style={{fontSize:30,color: "red",marginLeft: 10}}></i>
              </td>
            </tr>
          </tbody>
        )) }
      </Table>
      </div>
     
      
    </div>
        </>
    )
}
export default AdminProductsList;