import { Link, useLocation, useNavigate } from "react-router-dom";

function OrderSuccessPage({loginUser}){
    const location = useLocation();
    const navigate = useNavigate();
    if(!loginUser){
        navigate('/login')
    }

    return(
        <>
        <div class="container my-5">
        <div>
            <h1 class="text-center">E-shopper</h1>
        </div>

        <div class="d-flex justify-content-center">
           

            <div class="login-box text-center">
                <h3 class="text-center">Order {location.state}</h3>
                    <h3>Successful</h3>
                <i class="bi bi-check-circle-fill text-success success-icon"></i>
                 
                <h6>We will deliver your order in some days</h6>
                <Link to="/myorders">Go To My Orders</Link>
            </div>

        </div>
   
    </div>
        </>
    )
}
export default OrderSuccessPage;