import axios from "axios";
import { useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

function EditProduct({updateProduct}){
    const location = useLocation()
    const navigate = useNavigate()
    const product = location.state.product;
    const [editProduct,setEditProduct] = useState({});
    function handleChange(e){
        setEditProduct({...editProduct,[e.target.name]:e.target.value})
    }
    function handleSubmit(){
        updateProduct(editProduct,product._id);
        toast.success('Product details editted Successfully', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
          });
        navigate("/adminProducts")
    }
    return(
        <>
        <h1>EDIT PRODUCT HERE</h1>
        <div class="container my-5">
        <div>
          <a href="/" class="logo">
            <h1 class="text-center">e-Shopper</h1>
          </a>
        </div>

        <div class="d-flex justify-content-center">
          <div class="login-box m-auto mt-5 col-4">
            <h3 class="text-center">Create Product</h3>
            <form
              class="needs-validation"
              onSubmit={(e) => {
                e.preventDefault();
              }}
            >
              <div>
                <label for="email" class="form-label">
                  Name
                </label>
                <input
                  type="name"
                  class="form-control"
                  placeholder="Enter Product Name"
                  name="name"
                  defaultValue={product.name}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label  class="form-label">
                  Price
                </label>
                <input
                  type="number"
                  class="form-control"
                  placeholder="Price"
                  name="price"
                  defaultValue={product.price}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label class="form-label">
                  Category
                </label>
                <input
                  type="text"
                  class="form-control"
                  placeholder="Category"
                  name="category"
                  defaultValue={product.category}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label  class="form-label">
                  No. of Stock
                </label>
                <input
                  type="number"
                  class="form-control"
                  placeholder="Number of Stock"
                  name="stock"
                  defaultValue={product.stock>=0?product.stock:0}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label for="password" class="form-label">
                 Details
                </label>
                <input
                  type="text"
                  class="form-control"
                  placeholder="Details"
                  name="details"
                  defaultValue={product.details}
                  onChange={handleChange}
                />
              </div>
              {/* <div>
                <label for="password" class="form-label">
                  Choose Image
                </label>
                <input
                  class="form-control"
                  placeholder="image"
                  name="image"
                  defaultValue={product.image}
                  type="text"
                  onChange={handleChange}
                />
              </div> */}
                {/* <img style={{width:100}} src={createProduct.image}/> */}
                {/* <div>
                <label for="password" class="form-label">
                  Choose Additional Images
                </label>
                <input
                  class="form-control"
                  placeholder="Category"
                  name="image1"
                  defaultValue={product.image1}
                  type="text"
                  onChange={handleChange}
                />
                <input
                  class="form-control"
                  placeholder="Category"
                  name="image2"
                  defaultValue={product.image2}
                  type="text"
                  onChange={handleChange}
                />
                <input
                  class="form-control"
                  placeholder="Category"
                  name="image3"
                  defaultValue={product.image3}
                  type="text"
                  onChange={handleChange}
                />
              </div> */}
               
              <input
                type="submit"
                class="form-control btn-success"
                value="Edit"
                onClick={handleSubmit}
              />
            </form>
            
          </div>
        </div>
      </div>
        </>
    )
}
export default EditProduct;