import { Link } from "react-router-dom";
import AdminDashboard from "./adminDashboard";
import Table from 'react-bootstrap/Table';
import { useState, useEffect } from "react";
import axios from 'axios';
import SideBar from "./Dashboard/SideBar";

function AdminOrdersList({allOrders,deleteOrder}){
    let i=1;
   
    return(
        <>
        <AdminDashboard></AdminDashboard>
       
        <div className="userTable">
          <SideBar></SideBar>
          <div style={{width:"100%"}}>
          <h1>Orders List</h1>
        <Table responsive="sm">
        <thead>
          <tr>
            <th>#</th>
            <th>Order No</th>
            <th>User Id</th>
            <th>Name of User</th>
            <th>Status</th>
            <th>Items Qty.</th>
            <th>Amount</th>
            <th>Actions</th>
          </tr>
        </thead>
         {allOrders.length>0 ? allOrders.map(order=>order.items.map(v=>
         
         <tbody>
         <tr>
           <td>{i++}</td>
           <td>{v[v.length-1].orderId}</td>
           <td>{order.userId}</td>
           <td>{v[v.length-1].firstName+" "+ v[v.length-1].lastName}</td>
           {v[v.length-1].status==="Processing"?<td style={{color:"red"}}>{v[v.length-1].status}</td>:v[v.length-1].status==="Shipped"?<td>{v[v.length-1].status}</td>:<td style={{color:"rgb(50, 247, 50)"}}>{v[v.length-1].status}</td>}
           <td>{v[v.length-1].quantity}</td>
           <td>{parseFloat((v[v.length-1].amount)).toFixed(2)}</td>
          
           <td> 
           <Link to={`/orderDetails/${v[v.length-1].orderId}`} state={{order:v,userOrder:order}}><i className="fa fa-edit hover" style={{fontSize:28}}></i></Link>
           <i className="fa fa-trash-o hover" onClick={()=>deleteOrder(order,order.userId)} style={{fontSize:30,color: "red",marginLeft: 10}}></i>
           </td>
         </tr>
       </tbody>
            
            )):null} 
            
       
      </Table>
      </div>
      </div>
        </>
    )

}
export default AdminOrdersList;