import axios from "axios";
import { useState } from "react";
import AdminDashboard from "./adminDashboard";
import "./createProduct.css"
import SideBar from "./Dashboard/SideBar";
import { toast } from "react-toastify";
axios.defaults.withCredentials = true;

function CreateProducts({product,setProduct}) {
  const initialProduct = {
    name:"",
    price:"",
    category:"",
    stock:"",
    details:"",
    image:"",
    image1:"",
    image2:"",
    image3:""
  }
  const [createProduct,setCreateProduct] = useState(initialProduct);
  const [image , setImage] = useState([]);

  function handleChange(e) {
    setCreateProduct({...createProduct,[e.target.name]:e.target.value});
  }
  function handleSubmit(e){
    e.preventDefault()
    if(!createProduct.name||!createProduct.price||!createProduct.category||!createProduct.stock||!createProduct.details||!createProduct.details){
      toast.error('Please fill all valid fields', {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "colored",
        });
    }else{
    setImage([]);
    axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/createProduct',createProduct).then(function (response) {
            // handle success
            if(response.status===200){
              setProduct([...product,createProduct]);
              setCreateProduct(initialProduct); 
              toast.success('Product Created Successfully', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });           
             }
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })}  
  }

  function imageChange(e){
    e.preventDefault()
    if(image.length<4){
    let formData = new FormData();
    formData.append('image', e.target.files[0]);
    axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/photo',formData).then(function (response) {
      // handle success
      if(response.status===200){
        setImage([...image,response.data])
       }
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })  
  }else{
    toast.warn('You can only choose 4 images for each product', {
      position: "top-right",
      autoClose: 3000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "colored",
      });
  }
}
  
  return (
    <>
    <AdminDashboard></AdminDashboard>
    <div style={{display:'flex',paddingTop:16}}>
      <SideBar></SideBar>
      <div className="container my-5">
    
        <div className="d-flex">
          <div className="login-box m-auto col-4">
            <h3 className="text-center">Create Product</h3>
            <form
              className="needs-validation"
    
              onSubmit={(e) => {
                e.preventDefault();
              }}
            >
              <div>
                <label >
                  Name
                </label>
                <input
                  type="name"
                  className="form-control"
                  placeholder="Enter Product Name"
                  name="name"
                  value={createProduct.name}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label  >
                  Price
                </label>
                <input
                  type="number"
                  className="form-control"
                  placeholder="Price"
                  name="price"
                  value={createProduct.price}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label >
                  Category
                </label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Category"
                  name="category"
                  value={createProduct.category}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label  >
                  No. of Stock
                </label>
                <input
                  type="number"
                  className="form-control"
                  placeholder="Number of Stock"
                  name="stock"
                  value={createProduct.stock}
                  onChange={handleChange}
                />
              </div>
              <div>
                <label >
                 Details
                </label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Details"
                  name="details"
                  value={createProduct.details}
                  onChange={handleChange}
                />
              </div>
              {/* <div>
                <label for="password" className="form-label">
                  Choose Image
                </label>
                <input
                  className="form-control"
                  placeholder="image"
                  name="image"
                  value={createProduct.image}
                  type="text"
                  onChange={handleChange}
                />
              </div> */}
              <div>
                <label >
                  Choose Image
                </label>
                <input
                  className="form-control"
                  placeholder="image"
                  name="image"
                  // value={"Product Images"}
                  type="file"
                  onChange={imageChange}
                />
              </div>
              <div className="chooseImage">
                {image[0]?<img  src={`https://ecommerce-mern-stack-5vy4.onrender.com/${image[0]}`}/>:null}
                {image[1]?<img  src={`https://ecommerce-mern-stack-5vy4.onrender.com/${image[1]}`}/>:null}
                {image[2]?<img  src={`https://ecommerce-mern-stack-5vy4.onrender.com/${image[2]}`}/>:null}
                {image[3]?<img  src={`https://ecommerce-mern-stack-5vy4.onrender.com/${image[3]}`}/>:null}
                </div>
                {/* <div>
                <label >
                  Choose Additional Images
                </label>
                <input
                  className="form-control"
                  placeholder="Category"
                  name="image1"
                  value={createProduct.image1}
                  type="file"
                  onChange={handleChange}
                />
                <input
                  className="form-control"
                  placeholder="Category"
                  name="image2"
                  value={createProduct.image2}
                  type="text"
                  onChange={handleChange}
                />
                <input
                  className="form-control"
                  placeholder="Category"
                  name="image3"
                  value={createProduct.image3}
                  type="text"
                  onChange={handleChange}
                />
              </div>
                */}
              <input
                type="submit"
                className="form-control btn-success"
                value="Create"
                onClick={handleSubmit}
              />
            </form>
            
          </div>
        </div>
      </div>
      </div>
    </>
  );
}
export default CreateProducts;
