import { useEffect } from "react";
import { Link } from "react-router-dom";
import axios from 'axios';
import { format } from 'date-fns';
axios.defaults.withCredentials = true;


function Order({ orderProduct,setOrderProduct,cartProduct,shippAddress}){
  const totalCost = cartProduct.reduce((total,value)=>total+value.price,0);
  let time=new Date(orderProduct.updatedAt)

    return(
        <>{Object.keys(orderProduct).length !== 0 && orderProduct.items.length>0?<h1 id="orderHeading">My Orders</h1>:
        <div style={{display:'flex',width:'100vw',height:'60vh',alignItems:'center', justifyContent:"center"}}>
        <h1 style={{color:'grey'}}>No Orders</h1>
    </div>}
           
          <div class="container mb-5">
      <div class="d-flex flex-row align-items-start">
        <div id="orderItem" class="col-8 d-flex flex-column m-2">
          
          {orderProduct.items.length > 0 ?  orderProduct.items.map(v=><div>
            <h4>Order no. : {v[v.length-1].orderId}</h4>
            <span style={{padding:0}}>Date of Order : {format(new Date(orderProduct.updatedAt), 'dd/MMM/yyyy')}
               |   Time : {v[v.length-1].hours} : {v[v.length-1].minutes} : {v[v.length-1].seconds}</span>
            {v.slice(0,-1).map(value=>
            <div class="cart-item p-1 m-1">
            <div class="d-flex flex-row">
              <img
                style={{margin:0}}
                id="orderImage"
                class="col-2 img-fluid"
                // src={`images/${value.image}.jpg`}
                src={`https://ecommerce-mern-stack-5vy4.onrender.com/${value.image[0]}`}
                alt=""
              />
              <div id="orderName" class="col-6 p-2">
                <h5 id="orderName">{value.name}</h5>
                <h6 id="orderName">{value.category}</h6>
                <p id="orderName">${value.price}</p>
                <p id="orderName">Quantity: {value.quantity}</p>
                
              </div>

              {v[v.length-1].status==="Processing"?<p id="status"><strong >Status :</strong><span style={{color:"red"}}> {v[v.length-1].status}</span></p>:v[v.length-1].status==="Shipped"?<p id="status"><strong>Status :</strong>  {v[v.length-1].status}</p>:<p id="status"><strong>Status :</strong><span style={{color:"rgb(50, 247, 50)"}}> {v[v.length-1].status}</span></p>}
            </div>
            {/* <div> Order Cost : ${(totalCost-totalCost*10/100)+50}</div> */}
          </div>)}
          <div style={{marginBottom: 50}}> <strong> Order Cost :</strong> ${(totalCost-totalCost*10/100)+50}  |   
          <strong>  Name : </strong> {v[v.length-1].firstName } {v[v.length-1].lastName } | <strong>  Address : </strong>  {v[v.length-1].address1 }, {v[v.length-1].state }, {v[v.length-1].country }, {v[v.length-1].pinCode }
           <hr></hr>
           </div>
          </div>
          )
          :<span></span>}
        </div>
      </div>
    </div>
        </>
    )
}
export default Order;