import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import axios from 'axios';
import useMobileDevice from "../hook/useMobileDevice";
import Swal from "sweetalert2";

function PrivateRoute({setshowLoader}){
 const navigate = useNavigate();
 const isMobileDevice = useMobileDevice();
 let initialValue = {
  adminId :'',
  adminPassword:''
 }
 const [adminPassword , setAdminPassword] = useState(initialValue);

 function handleChange(e){
  setAdminPassword({...adminPassword,[e.target.name]:e.target.value})
 }
    function handleSavePass(){
       if(!isMobileDevice){
        if(!adminPassword.adminId|| !adminPassword.adminPassword){
          toast.error('Please enter valid fields', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
        }
        else{
          setshowLoader(true);
          axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/adminIdPassword',adminPassword).then(function (response) {
        // handle success
        if(response.data.status){
          navigate('/admin')
          setAdminPassword(initialValue);
          setshowLoader(false)
         }else{
          toast.error('Incorrect details', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "colored",
            });
            setshowLoader(false)
         }
      })
      .catch(function (error) {
        // handle error
        setshowLoader(false)
        console.log(error);
      })
          
        }
    }else{
      Swal.fire({
        title: 'Admin Page is only available for desktop mode ,please switch to desktop mode',
        showClass: {
          popup: 'animate__animated animate__fadeInDown'
        },
        hideClass: {
          popup: 'animate__animated animate__fadeOutUp'
        }
      })
    }
  }

    return(
        <>
       
        <div class="container my-5" >
        <div>
          <a href="/" class="logo">
            <h1 class="text-center">e-Shopper</h1>
          </a>        
        </div>

        <div class="d-flex justify-content-center">
           

            <div class="login-box  mt-5">
                <h3 class="text-center">Admin</h3>
                <form class="needs-validation" onSubmit={e=>{e.preventDefault();handleSavePass()}}>

                  <div>
                    <label for="email" class="form-label">Admin Id</label>
                    <input type="email" class="form-control" placeholder="Admin ID" name="adminId" value={adminPassword.adminId} id="adminId" onChange={handleChange}/>
                    <div class="invalid-feedback">
                      Valid Email required
                    </div>
                  </div>
                  <div>
                    <label for="password" class="form-label"> Password</label>
                    <input type="password" class="form-control" id="adminPassword" value={adminPassword.adminPassword} placeholder="password"  minlength="6" name="adminPassword" onChange={handleChange} />
                    <div class="invalid-feedback">
                      Valid Password required (min 6 chars)
                    </div>
                  </div>
          
                    <input style={{marginTop:20,color:"white", backgroundColor:'orangered'}} type="submit" class="form-control btn-success"/>
                </form>
            </div>

        </div>
   
    </div>
        </>
    )
}
export default PrivateRoute;