import { useState,useEffect } from "react";
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
axios.defaults.withCredentials = true;

function Checkout({cartProduct  , setOrderProduct ,loginUser,shippAddress,setShippingAddress,ListOfAddress, setListAddress ,emptyCart ,setshowLoader}){
  const navigate = useNavigate()
  let totalCost=0;
  let totalQuantity=0;
  function total(value){
    totalCost=totalCost+(value.price*value.quantity)
    totalQuantity=totalQuantity+value.quantity
  }
  if(cartProduct.length>0){
    cartProduct.map(total)
  }else{

  }
    const initialAddress = {
        firstName : "",
        lastName : "",
        phone : "",
        address1 : "",
        address2 : "",
        country : "",
        state : "",
        pinCode : ""
    };
    const [address , setAddress] = useState(initialAddress);

    function handlePlace(e){
      e.preventDefault();
      if(shippAddress===undefined){
        toast.warn('Please select address first', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          });
      }else{
        let d = new Date();
        const orderAddress = {
          orderId : (new Date().getTime()).toString(),
          firstName: shippAddress.firstName,
          lastName:shippAddress.lastName,
          phone: shippAddress.phone,
          status :"Processing",
          address1:shippAddress.address1,
          country:shippAddress.country,
          state:shippAddress.state,
          pinCode:shippAddress.pinCode,
          quantity:totalQuantity,
          amount:(totalCost-totalCost*10/100)+50-5,
          hours: d.getHours().toString(),
          minutes:d.getMinutes().toString(),
          seconds:d.getSeconds().toString()
        }
       cartProduct.push(orderAddress)
          setshowLoader(true);
        axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/orders',cartProduct).then(function (response) {
          // handle success
          if(response.status===200){
            setOrderProduct(response.data)
            navigate('/orderSuccess',{state:cartProduct[cartProduct.length-1].orderId})
            emptyCart();
            setshowLoader(false);
           }
        })
        .catch(function (error) {
          // handle error
          setshowLoader(false);
          console.log(error);
        })  

        
        // setOrderProduct(cartProduct);
       
      }
    }
    
    function handleChange(e){
        setAddress({ ...address, [e.target.name]: e.target.value });
    }
    function handleSubmit(e){
        e.preventDefault();        
          if(!address.firstName ||!address.lastName ||!address.phone ||!address.address1 ||!address.country||!address.state||!address.pinCode) {
           toast.warn('All fields should be filled', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
          });
        }else{
          address.userId = loginUser._id
          address.id = (new Date().getTime()).toString(36)
          setshowLoader(true);
          axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/address',address).then(function (response) {
            // handle success
            if(response.status===200){
              setListAddress(response.data.items)
              setAddress(initialAddress);
              window.scrollTo(0, 0)
              toast.success('Address added Successfully', {
                position: "top-right",
                autoClose: 1000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
                setshowLoader(false);
             }
          })
          .catch(function (error) {
            // handle error
            setshowLoader(false);
            console.log(error);
          })  
       
        }
    }

    
     
   function deleteAddress(id){
    setshowLoader(true);
    axios.delete(`https://ecommerce-mern-stack-5vy4.onrender.com/deleteAddress/${id}`).then(function (response) {
      // handle success
      if(response.status===200){
        setListAddress(ListOfAddress.filter((value) => value.id !==id)); //this filter make a copy of array
        setShippingAddress();
        setshowLoader(false);
       }
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })
   } 

    function shippingAddress(value){
        setShippingAddress(value);   
    }
    return(
        
        <div class="container mb-5">
        <main>
          <div class="py-5 text-center">
            <h2>Checkout</h2>
          </div>
      
          <div class="row g-3">
            <div class="col-md-5 col-lg-4 order-md-last">
              <h4 class="d-flex justify-content-between align-items-center mb-3">
                <span class="text-muted">Your cart</span>
                <div>
                <span class="text-muted">Items </span>
                <span class="badge bg-secondary rounded-pill">{cartProduct.length}</span></div>
              </h4>
              <ul class="list-group mb-3">
                <li class="list-group-item d-flex justify-content-between lh-sm">
                  <div>
                    <h6 class="my-0">Total</h6>
                    <small class="text-muted">{totalQuantity} Quantities</small>
                  </div>
                  <span class="text-muted">${(totalCost-totalCost*10/100)+50}</span>
                </li>
               
                <li class="list-group-item d-flex justify-content-between bg-light">
                  <div class="text-success">
                    <h6 class="my-0">Promo code</h6>
                    <small>EXAMPLECODE</small>
                  </div>
                  <span class="text-success">$5</span>
                </li>
                <li class="list-group-item d-flex justify-content-between">
                  <span>Total (USD)</span>
                  <strong>${(totalCost-totalCost*10/100)+50-5}</strong>
                </li>
              </ul>
              
             {shippAddress && ListOfAddress.length>0 ?<div class="card">
                <div class="card-body">
                  <h5 class="card-title">{shippAddress.firstName} {shippAddress.lastName}</h5>
                  <h6 class="card-subtitle mb-2 text-muted ">{shippAddress.address1}, {shippAddress.state}, {shippAddress.country}, {shippAddress.pinCode}</h6>
                  <p class="card-text">{shippAddress.phone}</p>
                  
                </div>
              </div>:null}
            </div>
            

            


            <div class="col-md-7 col-lg-8">
              <h4 class="mb-3">Shipping address</h4>
            {ListOfAddress.length > 0 ?ListOfAddress.map(value=><div
            id= {shippAddress && value.id===shippAddress.id?"choosedAddress":"listOfAddress"}
            // style={{border:2,borderColor:'orangered',borderStyle:'solid',marginBottom:10}}
            class="card"
            onClick={()=>shippingAddress(value)}
            >
                <div class="card-body">
                  <h5 class="card-title">{value.firstName} {value.lastName}</h5>
                  <h6 class="card-subtitle mb-2 text-muted ">{value.address1}, {value.state}, {value.country}, {value.pinCode}</h6>
                  <p class="card-text">{value.phone}</p>
                  <input style={{cursor:'pointer'}} type="radio" checked={shippAddress&&shippAddress.id===value.id} name="address"  onClick={()=>shippingAddress(value)}/> Use this Address
                  <span style={{color:'red',marginLeft:26,cursor:'pointer'}} onClick={()=>deleteAddress(value.id)}>Remove</span>
                </div>
                
              </div>):null}  

            
              <hr class="my-4"/>
              <h5>OR</h5>

              <h4 class="mb-3">Add New Address</h4>

              <form class="needs-validation" novalidate="" action="/order-success.html">
                <div 
                id="address"
                // style={{border:4,borderColor:'orangered',borderStyle:'solid',padding:25,borderRadius:60,marginTop:16}} 
                class="row g-3">
                  <div class="col-sm-6">
                    <label for="firstName" class="form-label">First name</label>
                    <input class="form-control" type="text"  id="addressInputs" name="firstName" value={address.firstName} onChange={handleChange}/>
                    <div class="invalid-feedback">
                      Valid first name is required.
                    </div>
                  </div>
      
                  <div class="col-sm-6">
                    <label for="lastName" class="form-label">Last name</label>
                    <input type="text" class="form-control" id="addressInputs" name="lastName" value={address.lastName} onChange={handleChange}/>
                    <div class="invalid-feedback">
                      Valid last name is required.
                    </div>
                  </div>
      
      
                  <div class="col-12">
                    <label for="phone" class="form-label">Phone <span class="text-muted"></span></label>
                    <input type="tel" class="form-control" id="addressInputs" placeholder="+91-1111111111" name="phone" value={address.phone} onChange={handleChange} />
                    <div class="invalid-feedback">
                      Please enter a valid phone for shipping updates.
                    </div>
                  </div>
      
                  <div class="col-12">
                    <label for="address" class="form-label">Address</label>
                    <input type="text" class="form-control" id="addressInputs" placeholder="1234 Main St" name="address1" value={address.address1} onChange={handleChange}/>
                    <div class="invalid-feedback">
                      Please enter your shipping address.
                    </div>
                  </div>
      
                  <div class="col-12">
                    <label for="address2" class="form-label">Address 2 <span class="text-muted">(Optional)</span></label>
                    <input type="text" class="form-control" id="addressInputs" placeholder="Apartment or suite"  name="address2" value={address.address2} onChange={handleChange}/>
                  </div>
      
                  <div class="col-md-5">
                    <label for="country" class="form-label">City</label>
                    <select class="form-select" id="addressInputs" name="country" value={address.country} onChange={handleChange}>
                      <option value="">Choose...</option>
                      <option>Latur</option>
                      <option>Pune</option>
                      <option>Mumbai</option>
                      <option>Hyderabaad</option>
                    </select>
                    <div class="invalid-feedback">
                      Please select a valid country.
                    </div>
                  </div>
      
                  <div class="col-md-4">
                    <label for="state" class="form-label">State</label>
                    <select class="form-select" id="addressInputs" name="state" value={address.state}onChange={handleChange}>
                      <option value="">Choose...</option>
                      <option>Maharashtra</option>
                      <option>Karnataka</option>
                    </select>
                    <div class="invalid-feedback">
                      Please provide a valid state.
                    </div>
                  </div>
      
                  <div class="col-md-3">
                    <label for="zip" class="form-label">Zip</label>
                    <input type="text" class="form-control" id="addressInputs" name="pinCode" value={address.pinCode} onChange={handleChange}/>
                    <div class="invalid-feedback">
                      Zip code required.
                    </div>
                  </div>
                </div>
      
                <hr class="my-4"/>
    
                <button style={{backgroundColor:'orangered',border:'none'}} class="w-100 btn btn-success btn-lg" onClick={handleSubmit}>Add Adress</button>

                <button style={{backgroundColor:'rgb(33,37,41)',border:'none'}} class="w-100 btn btn-primary btn-lg mt-4" onClick={handlePlace}>Place Order</button>
              </form>
            </div>
          </div>
        </main>
    
      </div>
    )
}

export default Checkout;