import Nav from "../components/Navbar";
import Footer from "../components/footer";
import ProductDetails from "../components/productDetails";
import { useLocation, useNavigate } from "react-router-dom";


function ProductDetailPage({loginUser, cartProduct, addToCart, setLoginUser,setshowLoader}) {
    const location = useLocation();
    const product = location.state.productDetail;
    const navigate = useNavigate();
    if(!loginUser){
        navigate('/login')
    }
  return (
    <>
      <Nav cartCount={cartProduct.length} setLoginUser={setLoginUser} setshowLoader={setshowLoader} ></Nav>
      <ProductDetails product={product} addToCart={addToCart}></ProductDetails>

      <Footer></Footer>
    </>
  );
}

export default ProductDetailPage;
