import axios from "axios";
import { Link} from "react-router-dom";
import { toast } from "react-toastify";
import styles from './cart.module.css'

function Cart({cartProduct ,newCartProduct,deleteCartItem,showLoader,setshowLoader,product}){
  let totalCost=0;
  let totalQuantity=0;
  // let outOfStock=0;
  function total(value){
    totalCost=totalCost+(value.price*value.quantity)
    totalQuantity=totalQuantity+value.quantity
  }
  if(cartProduct.length>0){
    cartProduct.map(total)
  }else{

  }

  function increaseQuantity(value){
    console.log(value)
    if(value.quantity<value.stock){
    setshowLoader(true);
    value.quantity=value.quantity+1;
    axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/cart',value).then(function (response) {
      newCartProduct(response.data.items); 
      setshowLoader(false);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
    }else{
      toast.warn(`Only ${value.stock} stock of this items are available`, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        });
    }  
  }
  function decreaseQuantity(value){
    if(value.quantity>1){
    setshowLoader(true);
    value.quantity=value.quantity-1;
      axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/cart',value).then(function (response) {
        console.log(response)
        newCartProduct(response.data.items); 
        setshowLoader(false);
      })
      .catch(function (error) {
        console.log(error);
        setshowLoader(false);
      })  
    }
    
  }

    return(

    <>
    <div className={styles.section}>
      <div className={styles.container}>
        <div className={styles.cartItems}>
          <div className={styles.cartHeading}>
            <p>Product</p>
            <p id={styles.quantity}>Quantity</p>
            <p>Sub Total</p>
          </div>
          {cartProduct.length>0?cartProduct.map(value=><div className={styles.item}>
              <div className={styles.imgANDdetail}>
              <img src={`https://ecommerce-mern-stack-5vy4.onrender.com/${value.image[0]}`} alt=""/>
              <div className={styles.itemDetails}>
                <h5>{value.name}</h5>
                <h6>{value.category}</h6>
                <p>${value.price}</p>
                <p style={{color:'red'}}  onClick={()=>deleteCartItem(value._id)}>Remove</p>
              </div>
              </div>
              <div className={styles.quantity}>
               <h3 id={styles.minus} onClick={()=>decreaseQuantity(value)}>-</h3>
               <p>{value.quantity}</p>
               <h3 id={styles.plus} onClick={()=>increaseQuantity(value)}>+</h3>
              </div>
              <div className={styles.subTotal}>
                <h5>${value.price*value.quantity}</h5>
              </div>
            </div>):null}
        </div>
        <div className={styles.ItemsSummary}>
            <h4>Order Total</h4>
            <div className={styles.summaryLine}>
              <span>Total Items</span>
              <span>{cartProduct.length}</span>
            </div>
            <div className={styles.summaryLine}>
              <span>Total Quantities</span>
              <span>{totalQuantity}</span>
            </div>
            <div className={styles.summaryLine}>
              <span>Shipping</span>
              <span>$50</span>
            </div>
            <div style={{marginTop:10}} className={styles.summaryLine} id={styles.last}>
              <span>Total </span>
              <span>${(totalCost)+50}</span>
            </div>
            <Link to={'/checkout'}><button>Proceed to Checkout</button></Link>
        </div>
      </div>
    </div>
    </>
    )
}
export default Cart;