
import { BrowserRouter, Route, Routes, useLocation, useNavigate } from 'react-router-dom';
import './App.css';
import Home  from './pages/home';
import CartPage from './pages/cartPage';
import { useEffect, useState } from 'react';
import CheckoutPage from './pages/checkOutPage';
import OrderPage from './pages/orderPage';
import ProductDetailPage from './pages/productDetailPage';
import axios from 'axios';
import LoginSignupPage from './pages/loginSignupPage';
import OrderSuccessPage from './pages/orderSuccessPage';
import NoPage from './pages/404';
import CreateProducts from './Admin/createProducts';
import 'bootstrap/dist/css/bootstrap.min.css';
import AdminProductsList from './Admin/adminProductsList';
import EditProduct from './Admin/editProduct';
import AdminOrdersList from './Admin/adminOrdersList';
import AdminUsers from './Admin/adminUsers';
import UserProfile from './User/userProfile';
import OrderDetails from './Admin/orderDetails';
import Dashboard from './Admin/Dashboard/Dashboard';
import PrivateRoute from './PrivateRoutes/PrivateRoute';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Backdrop, CircularProgress } from '@mui/material';

axios.defaults.withCredentials = true;

function App() {
 
  const [cartProduct , newCartProduct] = useState([]);
  const [orderProduct , setOrderProduct] = useState({});
  const [loginUser , setLoginUser] = useState({});
  const [shippAddress , setShippingAddress]= useState();
  const [product , setProduct] = useState([]);
  const [showLoader,setshowLoader] = useState(false);
  const [allOrders,setAllOrders] = useState([]);
  const [allUsers, setAllUsers] = useState([]);
  
  useEffect(()=>{
    checkAuth();
    getProducts();
    getCartProducts();
    getOrders();
    getAllUsers()
    getAllUsersOrders()
  },[]) 
  const navigate = useNavigate();
  const location = useLocation();
  // console.log(loginUser)
  function checkAuth(){ 
    setshowLoader(true);
    axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/user').then(function (response) {
        // handle success
        if(response.data.status){
            setLoginUser(response.data.user);
            setshowLoader(false);
         }else{
            navigate('/login')
            setshowLoader(false);
         }
      })
      .catch(function (error) {
        // handle error
        setshowLoader(false);
        console.log(error);
       
      })  
  }

  function getAllUsers(){
    setshowLoader(true);
    axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/allUsers').then(function (response) {
      // handle success
      setAllUsers(response.data);
      setshowLoader(false);
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
  function updateUser(usr,id){
    setshowLoader(true);
    axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/editUser/${id}`,usr).then(function (response) {
      // handle success
      if(response.data.status){
        setAllUsers(response.data.result)
        toast.success('Save Changes Successfully', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "colored",
          });
          setshowLoader(false);
       }else{
        toast.warn('This Email is already present', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          });
          setshowLoader(false);
       }
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
  function deleteUser(id){
    setshowLoader(true);
    axios.delete(`https://ecommerce-mern-stack-5vy4.onrender.com/deleteUser/${id}`).then(function (response) {
      if(response.status===200){
        setAllUsers(allUsers.filter((value)=> value._id !==id)); //this filter make a copy of array   
        if(location.pathname!=='/profile'){
        toast.success('User deleted Successfully', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          });
        }
          if(loginUser._id===id){
        axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/logout').then(function (response) {
          // handle success
          if(response.status===200){
            setLoginUser({});
            navigate('/login');
           }
        })
        .catch(function (error) {
          // handle error
          console.log(error);
        
        }) 
      } 
      setshowLoader(false);
       }
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })
    axios.delete(`https://ecommerce-mern-stack-5vy4.onrender.com/deleteUserOrder/${id}`).then(function (response) {
      setshowLoader(true);
      if(response.status===200){
        setAllOrders(allOrders.filter(value=>value.userId !==id));
        setshowLoader(false);
       }
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })
    axios.delete(`https://ecommerce-mern-stack-5vy4.onrender.com/deleteUserAddress/${id}`).then(function (response) {
      setshowLoader(false);
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })
  }
  function getProducts(){
    setshowLoader(true);
    axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/product').then(function (response) {
      // handle success
      setProduct(response.data);
      setshowLoader(false);
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
  function updateProduct(editProduct,id){
    setshowLoader(true);
    axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/editProduct/${id}`,editProduct).then(function (response) {
      // handle success
      if(response.status===200){
        getProducts();
        setshowLoader(false);
       }
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
  function deleteProduct(id){
    setshowLoader(true);
    axios.delete(`https://ecommerce-mern-stack-5vy4.onrender.com/deleteProduct/${id}`).then(function (response) {
      // handle success
      if(response.status===200){
        setProduct(product.filter((value) => value._id !==id)); //this filter make a copy of array
        toast.success('Product deleted Successfully', {
          position: "top-right",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          });
          setshowLoader(false);
       }
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })
  }
  function getCartProducts(){
    setshowLoader(true);
    axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/cart').then(function (response) {
      // handle success
      newCartProduct(response.data)
      setshowLoader(false);
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
  
  function addToCart(product){
      product.userId = loginUser._id;
      setshowLoader(true);
      product.quantity=1;
      axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/cart',product).then(function (response) {
        // handle success
        if(response.status===200){
          newCartProduct(response.data.items); 
          setshowLoader(false);
          toast.success('Item Successfully added to cart', {
            position: "top-right",
            autoClose: 1000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            });
         }
      })
      .catch(function (error) {
        // handle error
        console.log(error);
        setshowLoader(false);
      })  
      
  // }
  } 
  
  function deleteCartItem(id){
    setshowLoader(true);
     axios.delete(`https://ecommerce-mern-stack-5vy4.onrender.com/delete/${id}`).then(function (response) {
        // handle success
        if(response.status===200){
          newCartProduct(response.data.items)
          setshowLoader(false);       
         }
      })
      .catch(function (error) {
        // handle error
        setshowLoader(false);
        console.log(error);
      })
     
  }
  function emptyCart(){
    setshowLoader(true);
    axios.delete('https://ecommerce-mern-stack-5vy4.onrender.com/emptyCart').then(function (response) {
        // handle success
        if(response.status===200){
          newCartProduct(response.data.items)
          setshowLoader(false);
         }
      })
      .catch(function (error) {
        // handle error
        setshowLoader(false);
        console.log(error);
      })
  }
  
  async function getOrders(){
    setshowLoader(true);
    await axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/orders').then(function (response) {
      setOrderProduct(response.data);
      setshowLoader(false);
     })
     .catch(function (error) {
       // handle error
       setshowLoader(false);
       console.log(error);
     })  
  }

  function getAllUsersOrders(){
    setshowLoader(true);
    axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/allUsersOrders').then(function (response) {
      // handle success
      setAllOrders(response.data);
      setshowLoader(false);
    })
    .catch(function (error) {
      // handle error
      setshowLoader(false);
      console.log(error);
    })  
  }
  function deleteOrder(order,id){
    setshowLoader(true);
    axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/deleteOrder/${id}`,order).then(function (response) {
        if(response.status===200){
          const index=allOrders.findIndex(value=>value._id===order._id)
          allOrders.splice(index,1,response.data) //splice method direct mutate the array and direct mutating the state in react dose not rerender the component
          //so we do some jugaad by copying the array and set it, this change the state and Render the Component
          let copy_allOrders_Array = allOrders;
          setAllOrders([...copy_allOrders_Array]);
          toast.success('Order deleted Successfully', {
            position: "top-right",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            });
            setshowLoader(false);
         }
      })
      .catch(function (error) {
        setshowLoader(false);
        console.log(error);
      })  
  }
  function orderStatus(userId,details,stockDetails){
    if(details.selectValue===undefined){
      toast.warn('Please Choose Order Status first', {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        });
    }else if(details.selectValue==="Shipped"){
      setshowLoader(true);
      axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/stockDetails',stockDetails).then(function (response) {
      })
      .catch(function (error) {
        console.log(error);
      })
      axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/orderStatus/${userId}`,details).then(function (response) {
        if(response.status===200){
          const index=allOrders.findIndex(value=>value.userId===userId)
          allOrders.splice(index,1,response.data) //splice method direct mutate the array and direct mutating the state in react dose not rerender the component
          //so we do some jugaad by copying the array and set it, this change the state and Render the Component
          let copy_allOrders_Array = allOrders;
          setAllOrders([...copy_allOrders_Array]);
          setshowLoader(false);
         }
      })
      .catch(function (error) {
        setshowLoader(false);
        console.log(error);
      })  
    }else{
      setshowLoader(true);
    axios.post(`https://ecommerce-mern-stack-5vy4.onrender.com/orderStatus/${userId}`,details).then(function (response) {
        if(response.status===200){
          const index=allOrders.findIndex(value=>value.userId===userId)
          allOrders.splice(index,1,response.data) //splice method direct mutate the array and direct mutating the state in react dose not rerender the component
          //so we do some jugaad by copying the array and set it, this change the state and Render the Component
          let copy_allOrders_Array = allOrders;
          setAllOrders([...copy_allOrders_Array]);
          setshowLoader(false);
         }
      })
      .catch(function (error) {
        setshowLoader(false);
        console.log(error);
      })  
    } 
  }


  return (
   <>
   {showLoader?<Backdrop
  sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
  open
  >
  <CircularProgress color="inherit" />
</Backdrop>:null}
   <ToastContainer />
   <Routes>
    <Route path='/' element={<Home product={product} setProduct={setProduct} cartProduct={cartProduct} newCartProduct={newCartProduct} addToCart={addToCart} setLoginUser={setLoginUser} getCartProducts={getCartProducts} setshowLoader={setshowLoader} ></Home>} />

    <Route path="cart" element={<CartPage product={product} loginUser={loginUser} setLoginUser={setLoginUser} cartProduct={cartProduct} newCartProduct={newCartProduct} deleteCartItem={deleteCartItem} showLoader={showLoader} setshowLoader={setshowLoader} ></CartPage>} />

    <Route path="checkout" element={
    window.history.state.idx!==0?<CheckoutPage setLoginUser={setLoginUser} loginUser={loginUser} cartProduct={cartProduct} orderProduct={orderProduct} setOrderProduct={setOrderProduct} shippAddress={shippAddress} setShippingAddress={setShippingAddress} emptyCart={emptyCart} setshowLoader={setshowLoader} ></CheckoutPage>:<NoPage></NoPage>} />

    <Route path="myorders" element={<OrderPage loginUser={loginUser} setLoginUser={setLoginUser} cartProduct={cartProduct} orderProduct={orderProduct} setOrderProduct={setOrderProduct} shippAddress={shippAddress} getOrders={getOrders} setshowLoader={setshowLoader}></OrderPage>} />

    <Route path="productDetails/:id" element={<ProductDetailPage cartProduct={cartProduct} addToCart={addToCart} setshowLoader={setshowLoader}></ProductDetailPage>} />

    <Route path="login" element={<LoginSignupPage setLoginUser={setLoginUser} getCartProducts={getCartProducts} setAllUsers={setAllUsers} allUsers={allUsers} setshowLoader={setshowLoader}></LoginSignupPage>} />

    <Route path="orderSuccess" element={
    window.history.state.idx!==0?<OrderSuccessPage loginUser={loginUser} ></OrderSuccessPage>:<NoPage></NoPage>} />
    
    <Route path='/adminPassword' element={<PrivateRoute setshowLoader={setshowLoader}/>}/>

    <Route path="admin" element={
    window.history.state.idx!==0?<Dashboard products={product} allUsers={allUsers} allOrders={allOrders} loginUser={loginUser}></Dashboard>:<NoPage/>} />

    <Route path="createProduct" element={
    window.history.state.idx!==0?<CreateProducts product={product} setProduct={setProduct}></CreateProducts>:<NoPage/>} />
    
    <Route path="adminProducts" element={
    window.history.state.idx!==0?<AdminProductsList products={product} deleteCartItem={deleteCartItem} deleteProduct={deleteProduct}></AdminProductsList>:<NoPage/>} />
    
    <Route path="editProduct/:id" element={
    window.history.state.idx!==0?<EditProduct updateProduct={updateProduct}></EditProduct>:<NoPage/>} />
    
    <Route path="adminOrders" element={
    window.history.state.idx!==0?<AdminOrdersList allOrders={allOrders} deleteOrder={deleteOrder}></AdminOrdersList>:<NoPage/>} />

    <Route path="adminUsers" element={
    window.history.state.idx!==0?<AdminUsers allUsers={allUsers} deleteUser={deleteUser}></AdminUsers>:<NoPage/>} />

    <Route path="orderDetails/:id" element={
    window.history.state.idx!==0?<OrderDetails orderStatus={orderStatus}></OrderDetails>:<NoPage/>} />
    
    <Route path="/profile" element={<UserProfile loginUser={loginUser} setLoginUser={setLoginUser} allUsers={allUsers} setAllUsers={setAllUsers} deleteUser={deleteUser} updateUser={updateUser} setshowLoader={setshowLoader} ></UserProfile>} />
    <Route path="*" element={<NoPage></NoPage>} />

    
   </Routes>
   </>
  );
}

export default App;
