
import { useNavigate } from "react-router-dom";
import Footer from "../components/footer";
import LoginSignup from "../components/LoginSignup/loginSignup";
import axios from 'axios';
import { useEffect } from "react";
import { toast } from "react-toastify";

axios.defaults.withCredentials = true;

function LoginSignupPage({login,signUp,setLoginUser,getCartProducts,setAllUsers,allUsers,setshowLoader}){
  
    const navigate = useNavigate();
    

    useEffect(()=>{
  
      checkAuth();       
     
    },[])
    function login(user){
      setshowLoader(true);
        axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/login',user).then(function (response) {
            // handle success
            if(response.status===200){
              setLoginUser(response.data.user);
              getCartProducts();
              navigate('/');
              toast.success('Login Successfully', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
                setshowLoader(false);
             }
          })
          .catch(function (error) {
            // handle error
            setshowLoader(false);
            console.log(error);
            toast.error('Incorrect credential', {
              position: "top-right",
              autoClose: 2000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              theme: "colored",
              });
          })  
    }
    function signUp(user){
      setshowLoader(true);
        axios.post('https://ecommerce-mern-stack-5vy4.onrender.com/signup',user).then(function (response) {
            // handle success
            if(response.status===200){
              setLoginUser(response.data.user);
              setAllUsers([...allUsers,response.data.user])
              navigate('/');
              toast.success('SignUp Successfully', {
                position: "top-right",
                autoClose: 2000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
                });
                setshowLoader(false);
             }
          })
          .catch(function (error) {
            // handle error
            setshowLoader(false);
            console.log(error);
            toast.error('User already exist', {
              position: "top-right",
              autoClose: 2000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
              theme: "colored",
              });
          })  
    }

    function checkAuth(){ 
      setshowLoader(true);
      axios.get('https://ecommerce-mern-stack-5vy4.onrender.com/user').then(function (response) {
          // handle success
          if(response.data.status){
            setLoginUser(response.data.user);
            navigate('/');
            setshowLoader(false);
           }else{
            navigate('/login');
            setshowLoader(false);
           }
        })
        .catch(function (error) {
          // handle error
          setshowLoader(false);
          console.log(error);
          navigate('/login');
        })  
    }
    
    return(
        <>
        
   <LoginSignup login={login} signUp={signUp}></LoginSignup>
   {/* <Footer></Footer>  */}
        </>
    )
}

export default LoginSignupPage;