import { useLocation, useNavigate } from "react-router-dom";
import orderStyles from "./orderDetails.module.css"
import { useState } from "react";
import { toast } from "react-toastify";
function OrderDetails({orderStatus}){
    const location = useLocation();
    const navigate = useNavigate();
    const o = location.state.order;
    const userId = location.state.userOrder.userId;
    const order = o[o.length-1];
    const [selectValue , setSelectValue] = useState();

    const stockDetails=[]
    o.slice(0,-1).map(val=>{
        stockDetails.push({productId:val._id,quantity : val.quantity});
    })
    function handleStatusChange(e){
        setSelectValue(e.target.value)
    }
    function handleSaveStatus(){
        orderStatus(userId,details,stockDetails)
       navigate("/adminOrders")
       toast.success(`Order status changes to ${details.selectValue}`, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        });
    }
    const details = {
        selectValue:selectValue,
        orderId:order.orderId
    }
   
    return(
        <>
        <div className={orderStyles.center}>
        <h1>Order Details</h1>
        <div className={orderStyles.container}>
            <div className={orderStyles.orderDetails}>
                <div className={orderStyles.shippingInfo}>
                    <h1>Shipping Info</h1>
                    <ul>
                        <li> <strong>Name :</strong> {order.firstName } {order.lastName }</li>
                        <li><strong>Phone :</strong> {order.phone}</li>
                        <li style={{width:400}}><strong> Address :</strong> {order.address1 }, {order.state }, {order.country }, {order.pinCode } </li>
                    </ul>
                </div>
                <div className={orderStyles.payment}>
                    <h1>Payment</h1>
                    <ul>
                    <strong><p style={{color:"rgb(50, 247, 50)"}}>PAID</p></strong>
                    <label><strong>Amount :</strong> Rs. {parseFloat((order.amount)).toFixed(2)}</label>
                    </ul>
                </div>
                <div className={orderStyles.status}>
                    <h1>Order Status</h1>
                    <ul>
                    {order.status==="Processing"?<p style={{marginTop:14,color:"red"}}>{order.status}</p>:order.status==="Shipped"?<p style={{marginTop:14}}>{order.status}</p>:<strong><p style={{marginTop:14,color:"rgb(50, 247, 50)"}}>{order.status}</p></strong>}
                    </ul>
                </div>
                <div className={orderStyles.items}>
                    <h1>Your Cart Items</h1>
                    {o.slice(0,-1).map(value=>
                     <div class="container mb-5" style={{width:700,height:105}}>
                     <div class="d-flex flex-row align-items-start">
                       <div class="col-8 d-flex flex-column m-2">
            <div class="cart-item p-3">
            <div class="d-flex flex-row" style={{height:120}}>
              <img
                class="col-2 img-fluid"
                // src={`images/${value.image}.jpg`}
                src={`https://ecommerce-mern-stack-5vy4.onrender.com/${value.image[0]}`}
                alt=""
              />
              <div class="col-6 p-2">
                <h5>{value.name}</h5>
                <h6>{value.category}</h6>
                <p>${value.price}</p>
                <p>Quantity: {value.quantity}</p>
              </div>
              
            </div>
            {/* <div> Order Cost : ${(totalCost-totalCost*10/100)+50}</div> */}
          </div>
          </div>   
              </div>
               </div> 
              
              )}
                </div>
            </div>
            
                {order.status==="Processing"?
                <div className={orderStyles.orderStatus}>
                <h1>Change Order Status</h1>
                <ul style={{marginTop:30}}>
                <select className="hover" value={selectValue} onChange={handleStatusChange}>
                    <option selected hidden>Choose Order Status</option>
                    <option>Shipped</option>
                </select>
                <button className={orderStyles.btn} onClick={handleSaveStatus}>Save Status</button>
                </ul> </div>:order.status==="Shipped"?
                <div className={orderStyles.orderStatus}>
                <h1>Change Order Status</h1>
                <ul style={{marginTop:30}}>
                <select className="hover" value={selectValue} onChange={handleStatusChange}>
                    <option selected hidden>Choose Order Status</option>
                    <option>Delivered</option>
                </select>
                <button className={orderStyles.btn} onClick={handleSaveStatus}>Save Status</button>
                </ul></div>:
                <div className={orderStyles.orderStatus}>
                <h1>Succefully Delivered</h1>
                <p style={{fontSize:20}}>This Order is Succefully Delevered to the user.</p></div>}
            </div>
       
        </div>
        </>
    )
}
export default OrderDetails;