import * as React from 'react';
import { PieChart } from '@mui/x-charts/PieChart';

export default function BasicPie({products}) {
  const arr = [];
  products.map(val=>{
    arr.push({value:val.stock, label: val.category })
  })
  return (
    
    <PieChart
    colors={['red', 'blue', 'green','yellow', 'brown']} // Use palette
      series={[
        {
          data: arr ,
        },
      ]}
      width={350}
      height={200}
    />
  );
}